<template>
  <div style="width:100%;height:100%">
    <div class="menu-top">
      <el-button class="el-icon-arrow-left return" @click="ret()"></el-button>客户详情
    </div>
    <div class="table-top radius_8">
      <div class="table-top-lef">
        <div>
          <img src="../../../assets/img/截屏2020-03-0716.28.22.png" width="100px" />
        </div>
        <div>
          <span class="font_size_20">{{customer.user_name}}</span>
          <span
            class="font_size_20"
            style="margin-left:10px;color:rgba(115, 136, 216, 1)"
          >{{customer.age?customer.age+'岁':''}}</span>
        </div>
        <div class="font_size_20">{{customer.user_tel}}</div>
        <!-- {{customer.create_time}} -->
        <div class="font_size_20">创建于{{customer.create_time}}</div>
      </div>
      <div class="table-top-cen">
        <span class="font_size_22 font_size_weight_600 table-top-cen-top">基本信息</span>
        <div class="table-top-cen-div">
          <div class="table-top-cen-span">
            <span class="font_size_20">客户状态</span>
            <span
              class="font_size_20"
              style="color:rgba(131, 208, 205, 1)"
            >{{customer.user_status?customer.user_status:'无' }}</span>
          </div>
          <div class="table-top-cen-span">
            <span class="font_size_20">获取渠道</span>
            <span
              class="font_size_20"
              style="color:rgba(131, 208, 205, 1)"
            >{{customer.channel?customer.channel:'无' }}</span>
          </div>
          <div class="table-top-cen-span">
            <span class="font_size_20">所属行业</span>
            <span
              class="font_size_20"
              style="color:rgba(131, 208, 205, 1)"
            >{{customer.trade?customer.trade:'无'}}</span>
          </div>
        </div>
        <div class="table-top-cen-div">
          <div class="table-top-cen-span">
            <span class="font_size_20">客户来源</span>
            <span
              class="font_size_20"
              style="color:rgba(131, 208, 205, 1)"
            >{{customer.source?customer.source:'无'}}</span>
          </div>
          <div class="table-top-cen-span">
            <span class="font_size_20">家庭人口</span>
            <span
              class="font_size_20"
              style="color:rgba(131, 208, 205, 1)"
            >{{customer.family?customer.family:0}}</span>
          </div>
          <div class="table-top-cen-span">
            <span class="font_size_20">所属职业</span>
            <span
              class="font_size_20"
              style="color:rgba(131, 208, 205, 1)"
            >{{customer.job?customer.job:'无'}}</span>
          </div>
        </div>
        <div class="table-top-cen-div">
          <!--  -->
          <div class="table-top-cen-span">
            <span class="font_size_20">居住现状</span>
            <span
              class="font_size_20"
              style="color:rgba(131, 208, 205, 1)"
            >{{customer.living_status?customer.living_status:'无'}}</span>
          </div>
          <div class="table-top-cen-span">
            <span class="font_size_20"></span>
            <span class="font_size_20" style="color:rgba(131, 208, 205, 1)"></span>
          </div>
          <div class="table-top-cen-span">
            <span class="font_size_20"></span>
            <span class="font_size_20" style="color:rgba(131, 208, 205, 1)"></span>
          </div>
        </div>
      </div>
      <div class="table-top-rig">
        <span class="font_size_22 font_size_weight_600 table-top-cen-top">维护信息</span>
        <div class="table-top-cen-div">
          <div class="table-top-cen-span">
            <span class="font_size_20">户籍</span>
            <span
              class="font_size_20"
              style="color:rgba(131, 208, 205, 1)"
            >{{customer.domicile?customer.domicile:'无'}}</span>
          </div>
          <div class="table-top-cen-span">
            <span class="font_size_20">居住地址</span>
            <span
              class="font_size_20"
              style="color:rgba(131, 208, 205, 1)"
            >{{customer.location?customer.location:'无'}}</span>
          </div>
        </div>
        <div class="table-top-cen-div">
          <div class="table-top-cen-span">
            <span class="font_size_20">购房用途</span>
            <span
              class="font_size_20"
              style="color:rgba(131, 208, 205, 1)"
            >{{customer.purpose?customer.purpose:'无'}}</span>
          </div>
          <div class="table-top-cen-span">
            <span class="font_size_20">房产</span>
            <span
              class="font_size_20"
              style="color:rgba(131, 208, 205, 1)"
            >{{customer.is_have==true?'有':'无'}}</span>
          </div>
        </div>
        <div class="table-top-cen-div">
          <div class="table-top-cen-span">
            <span class="font_size_20">本地数量</span>
            <span
              class="font_size_20"
              style="color:rgba(131, 208, 205, 1)"
            >{{customer.houses_num?customer.houses_num:0}}</span>
          </div>
          <div class="table-top-cen-span">
            <span class="font_size_20">外地数量</span>
            <span
              class="font_size_20"
              style="color:rgba(131, 208, 205, 1)"
            >{{customer.outside_houses_num?customer.outside_houses_num:0}}</span>
          </div>
        </div>
      </div>
    </div>

    <div class="menu-small-top radius_8">
      <span class="font_size_weight_600">跟进记录</span>
    </div>
    <div class="table-btn">
      <el-radio-group v-model="recording" @change="chaRecor()" class="table-btn-top">
        <el-radio-button v-for="item in recordingTabs" :key="item.value" :label="item.value"></el-radio-button>
      </el-radio-group>
      <div class="table-btn-span">
        <div class="table-btn-span-i" v-for="(item,index) in recordingData" :key="index">
          <div :class="item.title=='系统'?'table-btn-span-i-lef':'table-btn-span-i-red'"></div>
          <div class="table-btn-span-i-rig">
            <span
              class="font_size_16"
              style="color:rgba(116,134,216,1);"
            >{{item.title }} {{item.saller_name }} {{item.create_time }}</span>
            <br />
            <span class="font_size_16 bo_cen">{{item.memo }}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getCustomer, getMemos } from "@/api/getData";

export default {
  name: "followUpRecord",
  data() {
    return {
      recordingTabs: [
        { value: "全部跟进" },
        { value: "顾问操作" },
        { value: "系统操作" }
      ],
      recordingData: [],
      recordingAllData: [],
      recording: "全部跟进",
      customer: {},
      sel: {
        user_id: 0
      }
    };
  },
  components: {},
  computed: {},

  created() {
    this.sel.user_id = this.$route.query.user_id;
    this.changeDate();
  },
  activated() {
    // this.handleListener();
  },
  deactivated() {
    // window.removeEventListener('resize', this.renderChart);
    // bus.$off('collapse', this.handleBus);
  },

  methods: {
    async changeDate() {
      // 获取客户详情
      const res = await getCustomer(this.sel);
      //  console.log(res);
      if (res.code != 0) {
        this.$message({
          type: "error",
          message: res.msg
        });
        return;
      }
      this.customer = res.data.user;
      // 获取跟进记录
      const memos = await getMemos(this.sel);
      //  console.log(res);
      if (res.code != 0) {
        this.$message({
          type: "error",
          message: res.msg
        });
        return;
      }
      this.recordingData = memos.data;
      this.recordingAllData = memos.data;
      //
      
    },
    chaRecor() {
      if (
        Array.isArray(this.recordingAllData) &&
        this.recordingAllData.length > 0
      ) {
        if (this.recording == "顾问操作") {
          var list=this.recordingAllData.filter(
            x => x.item != "系统"
          );
          this.recordingData =list?list:[] ;
        } else if (this.recording == "系统操作") {
          var list=this.recordingAllData.filter(
            x => x.item == "系统"
          )
          this.recordingData = list?list:[];
        } else {
          this.recordingData = this.recordingAllData;
          // this.recordingData.concat(this.recordingAllData);
        }
      }
    },
    ret() {
      this.$router.push({
        path: "/cus",
        query: { sel: this.$route.params.sel }
      });
      //  this.$router.push('cus');
    }
  }
};
</script>


<style scoped>
.table-btn-span::-webkit-scrollbar {
  display: none;
}
.bo_cen {
  display: block;
  width: 100%;
  height: 60%;

  overflow: hidden;
  text-overflow: ellipsis;
}
.grid-content {
  background-color: black;
}
.table-top {
  width: 98%;
  padding: 1%;
  height: 35%;
  display: flex;

  align-items: center;
  justify-content: space-around;
}
.table-top-lef {
  width: 20%;
  height: 90%;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}
.table-top-rig {
  width: 22%;
  height: 100%;

  display: flex;
  flex-direction: column;
  padding: 0 10%;
}
.table-top-rig .table-top-cen-span {
  width: 100px;
  text-align: center;
}
.table-top-cen {
  width: 27%;
  height: 100%;

  display: flex;
  flex-direction: column;
  padding: 0 5%;
}
.table-top-cen-top {
  margin-bottom: 5px;
}
.table-top-cen-div {
  height: 25%;
  color: rgba(116, 134, 216, 1);
  display: flex;
  margin-top: 10px;
  align-content: center;
  justify-content: space-between;
}
.table-top-cen-span {
  width: 20%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.table-top-left-top,
.table-top-left-btn {
  height: 50%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.div_p {
  width: 26%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.inp {
  width: 200px;
}

.page {
  display: flex;
  height: 38px;
  margin-right: 10px;
  align-items: center;
  flex-direction: row-reverse;
  /* float: right; */
}
/* 修改 */
.upPaw {
  width: 90%;
  margin-left: 4%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.upPaw div span {
  font-size: 20px;
  /* font-weight: 600; */
  width: 35%;
}
.upPaw_pa {
  width: 100%;
  height: 30%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-top: 20px;
}
.set-role {
  margin-top: 20px;
  width: 100%;
  height: 30%;
  /* background: rosybrown; */
  display: flex;
}

.set-role-right {
  vertical-align: middle;
  width: 200px;
  /* background: royalblue; */
}
.table-btn {
  height: 44%;
  width: 98%;
  padding: 0 1%;
}
.table-btn-top {
  margin-bottom: 5px;
}
.table-btn-span {
  margin-top: 10px;
  height: 80%;
  width: 100%;
  /* 滚动 */
  overflow: scroll;
  overflow-x: hidden;
}
.table-btn-span-i {
  height: 30%;
  width: 100%;
  display: flex;
  margin-top: 10px;
}
.table-btn-span-i-lef {
  margin-top: 3px;
  width: 10px;
  height: 10px;
  border: 4px solid rgba(130, 208, 205, 1);
  border-radius: 50%;
}
.table-btn-span-i-red {
  margin-top: 3px;
  width: 10px;
  height: 10px;
  border: 4px solid rgba(240, 92, 132, 1);
  border-radius: 50%;
}
.table-btn-span-i-rig {
  width: 98%;
  margin-left: 10px;
  height: 100%;
  border-bottom: 1px rgba(0, 0, 0, 0.1) solid;
  /* background: rgb(92, 91, 90); */
}
.return {
  font-size: 24px;
  /* height: 40px; */
  padding: 0px;
  /* vertical-align: middle; */
  background: rgba(255, 255, 255, 0.2);
  margin-right:10px ;
}
</style>
<style >
.el-table__header {
  padding: 5px 0;
}
.el-table__body-wrapper .el-table__row td {
  padding: 5px 0;
}
.el-input__inner {
  height: 37px;
}
.el-input__icon {
  line-height: 37px;
}
.el-radio {
  margin-top: 5px;
}

/* .table-btn-top label{
   height: 30px;
} */
</style>



